import React, { Component } from 'react';

import "./Roadmunk.sass";
import Emoji from '../Emoji';
import { sendPayloadToSlack } from '../SlackBot';

export default class Roadmunk extends Component {
  
  componentDidMount(){
    sendPayloadToSlack({
      text: ' Roadmunk page openend'
    })
  }


  render() {
    return (
      <div>
          <h1><Emoji name='hello' /> Hi Roadmunk!</h1>
          
          <center style={{marginTop:'30px'}}>
            
            <div className="wistia_responsive_padding" style={{padding:'56.25% 0 0 0', position:'relative'}}>
              <div className="wistia_responsive_wrapper" style={{height:'100%',left:'0', position:'absolute', top:'0', width:'100%'}}>
                <div className="wistia_embed wistia_async_60aldy7vue seo=false videoFoam=true" style={{height:'100%', position:'relative', width:'100%'}} >
                  <div className="wistia_swatch" style={{height:'100%', left:'0', opacity:'0', overflow:'hidden', position:'absolute', top:'0', transition:'opacity 200ms', width:'100%'}}>
                    <img src="https://fast.wistia.com/embed/medias/60aldy7vue/swatch" style={{filter:'blur(5px)',height:'100%',objectFit:'contain', width:'100%'}} alt="" onLoad={(e)=>{console.log(e.target.parentNode); e.target.parentNode.style.opacity=1;}} />
              </div></div></div></div>
          </center>

          <h2>Thanks for watching!</h2>
          <p>I'm really excited about this opportunity and I hope we get to connect soon! Here's a few helpful links:</p>
          <br />
          <ul>
            <li>- <a href='mailto:igordepaula1@gmail.com' >My email</a></li>
            <li>- <a rel="noopener noreferrer" target='_blank' href='https://drive.google.com/open?id=1e4rRk0MTa1K0UGNJ1Vnd5yEMjZ7V73mU' >Download my resume</a></li>
            <li>- <a rel="noopener noreferrer" target='_blank' href='https://drive.google.com/open?id=1zBn3AHAb-K3Phw2JGpGPnuz95KEc9YQf' >Download my cover letter</a></li>
          </ul>

      </div>
    );
  }
}


