import { BrowserRouter as Router, Route, Switch  } from 'react-router-dom'
import React, { Component } from 'react';
import './App.sass';

import Emoji from './Emoji'
import Home  from './Home/Home'
import Career  from './Career/Career'
import Projects  from './Projects/Projects'
import Navigation  from './Navigation/Navigation'
import Roadmunk from './Roadmunk/Roadmunk';

const _404 = () => ( 
  <div>
    <h1>/404</h1>
    <h2><Emoji size='1.5em' name='shrug'/> This page doesn't exist</h2>
  </div>
  
)

class App extends Component {

  render() {
    return (
      <Router>
          <div>
              <Navigation />
              <div className="main-container">
                <div className='space-left' ></div>
                <div className='route-outlet'>
                  <Switch>
                    <Route exact path="/"     component={ Home } />
                    <Route exact path="/career"    component={ Career } />
                    <Route exact path="/projects"  component={ Projects } />
                    <Route exact path="/hello/roadmunk"  component={ Roadmunk } />
                    <Route component= { _404 } />
                  </Switch>
                </div>
                <div className='space-right'></div>
              </div>
          </div>
      </Router>
    );
  }
}

export default App;



